.c9 .woocommerce-Addresses {
	margin-top: 30px;
}

.c9 .woocommerce-Addresses .woocommerce-Address {
	max-width: 100%;
}

.c9 .select2-container--default .select2-selection--single {
	border-radius: 0px;
}

.woocommerce-page .col-1,
.woocommerce-page .col-2 {
	max-width: none;
}

.woocommerce-checkout .col-1,
.woocommerce-checkout .col-2 {
	padding-left: 0;
	padding-right: 0;
}

/* woocommerce date + time picker */
.ui-datepicker {
	width: 275px;
	padding: 6px;
	max-width: 100%;
}

.ui-corner-all,
.ui-corner-bottom,
.ui-corner-right,
.ui-corner-br {
	border-radius: 0px;
}

.ui-state-default,
.ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default {
	padding: 4px;
}

.ui-widget-header {
	background-image: none;
	background: $gray-500;
	color: $dark;
}

.ui-datepicker .ui-datepicker-prev,
.ui-datepicker .ui-datepicker-next {
	top: 50%;
	transform: translateY(-50%);
}

.ui-timepicker-wrapper,
.ui-datepicker-calendar,
.ui-datepicker .ui-datepicker-title {
	font-family: $font-family-base;
	font-size: 1.6em !important;
}

.ui-datepicker .ui-datepicker-title {
	text-transform: uppercase;
}

.ui-timepicker-list li {
	padding: 12px;
}

li.ui-timepicker-selected,
.ui-timepicker-list li:hover,
.ui-timepicker-list .ui-timepicker-selected:hover {
	background-color: $primary;
}

/* order confirmation */
ul.order_details li {
	font-size: 14px !important;
}

.woocommerce ul.order_details li strong {
	font-size: 16px !important;
}

.order_details:not(.blocks-gallery-grid):not(.wp-block-gallery):not(.wc-block-grid__products):not(.gfield_checkbox):not(.gfield_radio):not(.nav-tabs):not(.c9-share-list):not(.products):not(.gform_fields) li {
