/****************************************************************************************/
/* 404 error page */
/****************************************************************************************/
.error-404 .widget_recent_entries,
.error-404 .widget_archive {
	display: none;
}

.error-404 {
	margin: 155px auto 0px auto;
	text-align: center;
	max-width: 960px;
}

.error-404 .page-content p {
	font-size: 1.6em;
}

.error-404 h1 {
	font-size: 80px;
	text-align: center;
}

.error-404 .page-content .search-form {
	margin: 0px auto 80px auto;
	max-width: 60%;
}

.error-404 .page-content .search-form .btn-primary,
.search .page-content .search-form .btn-primary {
	color: $light;
	font-size: 2em;
	font-weight: 700;
	border: none;
