	margin-left: 20px;
	padding: 6px 30px 2px 30px !important;
	min-height: 55px;
}

.error-404 .page-content .search-form .input-group {
	display: flex;
	align-items: center;
	justify-content: center;
}

.error-404 .page-content .search-form .input-group.search-field {
	padding: 10px 12px;
	font-family: $font-family-sans-serif;
	font-size: 1.3em;
	letter-spacing: 0.02em;
	height: auto;
	border: 1px solid rgb(206, 212, 218);
}

.error404 #wrapper-navbar {
	background-color: $dark;
}

/****************************************************************************************/
/* Utility Classes */
/****************************************************************************************/
.img-shadow,
.is-style-img-shadow,
:not([data-align])>.wp-block-image.is-style-img-shadow {
	position: relative;
	display: block;
}

.is-style-img-shadow img,
.img-shadow img {
	z-index: 10;
}

.is-style-img-shadow.wp-block-image:after,
.img-shadow.wp-block-image:after {
	position: relative;
	bottom: -30px;
	z-index: 8;
	content: "";
	display: inline-block;
	width: 100%;
	height: 30px;
	background-image: radial-gradient(ellipse, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 68%);
	background-repeat: no-repeat;
}
